html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	// font-size: 100%;
	// font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
button {
  padding: 0;
  margin: 0;
  border:0;
  cursor: pointer;
}
* {box-sizing: border-box;}
a {text-decoration: none; color:#000}
// Reset css over ====================================================================================================
%flex_center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


body {
  // font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  //   'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  //   sans-serif;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  padding: 0 15px;
  max-width: 1060px;
  margin: 0 auto;
}

// ИЗМЕНИЛ ВАЖНО АЛО ТУТ ПОМЕНЯЙ ПРИЖМИ ФУТЕР МАТЕМАТИКА РАСЧЕТЫ КАЛК АЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕЕ
.content{
  min-height: calc(100vh - 70px - 50px);
  padding: 20px 0;
}

// HEADER ==================================================

header {
  background-color: #101010;
  .header {
    height: 70px;
    @extend %flex_center;
  }
  .header_logo {
    font-family: 'Alegreya Sans SC';
    color: white;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 60px;
    text-transform: uppercase;
  }
}

.header_btn {
  background-color: white;
  height: 40px;
  margin-right: 12px;
  transition: all .2s ease-out;
  &:last-child {
    margin-right: 0;
  }
  &:hover {
    background-color: #e74a2b;
    color: white;
  }
  padding: 11.5px 40.5px;
  color: black;
  font-size: 14px;
  font-weight: 600;
}
// MAIN TOP ==================================================

h3 {
  width: 400px;
  font-family: "Alegreya Sans SC";
  font-size: 35px;
  font-weight: 500;
  line-height: 40px;
  text-align: left;
  padding: 20px 0px 20px 15px;
}



.main_top {
  box-shadow: 0px -50px 19px -2px rgba(0, 0, 0, 0.5) inset;
  height: 439px;
  background: #000 url(img/topband.jpg) center center/cover no-repeat;
  // background: #000 url(img/topband.jpg) center center no-repeat;
  color: white;
  text-align: center;
  &_textarea {
    padding: 130px 0;
    background: radial-gradient(50% 40% at 50% 60%, #000000 0%, rgba(0, 0, 0, 0) 100%);
    font-family: 'Alegreya Sans SC';
    font-style: normal;
    font-weight: 700;
    h1 {
      font-size: 50px;
      line-height: 60px;
    }
    h2 {
      font-size: 30px;
      line-height: 36px;
    }
  }



  &_slider {
    @extend %flex_center;
    button {
      margin-right: 0;
    }
  }
  .slider_button {
    width: 40px;
    height: 40px;
    border: 1px solid white;
    font-size: 24px;
    user-select: none;   
    span {
      vertical-align: text-bottom;
    }
  }
  
}

// TICKETS ==================================================

.main_black_btn {
  width: 148px;
  height: 35px;
  background-color: #101010;
  border: 1px solid #0D1E4B;

  font-family: 'Open Sans';
  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  color: white;
  text-transform: capitalize;
  transition: all .2s ease;
  &:hover {
    background-color: #e74a2b;
  }

}
.tickets {
  &_info {
    @extend %flex_center;
    padding-top: 15px;
  }

  &_users {
    margin-top: 7px;
    @extend %flex_center;
    overflow-x: scroll;
    &::-webkit-scrollbar{height: 15px}



    &_person {
      width: 239px;
      height: 143px;
      border: 1px solid #000000;
      padding: 15px;
      margin-right: 10px;
      &:last-child{margin: 0}

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      h4 {
        font-size: 17px;
        line-height: 23px;
        text-transform: capitalize;
      }
      p {
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: rgba(0, 0, 0, 0.41);
      }
    }
  }
}

// ABOUT ==================================================


// .custom_scroll
::-webkit-scrollbar{width: 10px}
  
::-webkit-scrollbar-track{
    background: #b1b1b1;
    border-left: 4px solid white;
    border-right: 4px solid white;
  }
::-webkit-scrollbar-thumb{
    background: #0d1e4b;
  }




.main_about {
  margin: 47px 0;

  &_info {
    margin-bottom: 50px;
    @extend %flex_center;


    &_description {
      width: 326px;
      text-align: left;

      div {
        height: 230px;
        width: 380px;
        margin: 10px 0;
        padding: 10px 0 0 40px;


        overflow-y: scroll;
        direction: rtl;
      }

      h5 {
        font-size: 15px;
        line-height: 20px;
        margin-bottom: 20px;
      }
      p {
        color: #939393;
        font-size: 14px;
        line-height: 24px;
      }
    }
  
    &_form {
      width: 60%;
      height: 272px;
      border: 1px solid #DADADA;

      form {
        position: relative;
        padding: 15px;
        div {
          display: flex;
          flex-direction: column;
          span {
            font-size: 18px;
            line-height: 40px;
            text-align: left;
            padding-bottom: 10px;
          }
          input {
            height: 134px;
          }
        }
        button {
          position: absolute;
          width: 101px;
          right: 15px;
          bottom: -33px;
        }
      }
    }
  }


  &_band {
    @extend %flex_center;
    p {
      color: #939393;
      line-height: 24px;
      text-align: left;
  
    }
  }
}

// USER ==================================================

.user_info {
  border-top: 1px solid black;

  &_inner {

    &_basic {
      border-left: 1px solid #000;
      border-right: 1px solid #000;
      @extend %flex_center;
      p {
        font-size: 16px;
      }
      div:nth-child(1) {
        padding-left: 15px;
        @extend %flex_center;
        img {width: 50px}
      }

      div:nth-child(2) {
        max-width: 420px;
        padding: 15px;
        font-size: 18px;
        font-weight: 500;
        line-height: 22px;
        border-left: 1px solid black;
      }
    }


    &_rooftop {
      border: 1px solid #000;
      border-bottom: 0;
      display: flex;
      justify-content: space-around;
      align-content: stretch;
      align-items: center;
      height: 50px;
      div, a {
        font-size: 18px;
        font-weight: 400;
        line-height: 17px;
        text-align: left;
      }
  
    }
    &_splitter {
      background-color: black;
      height: 100%;
      width: 1px;
    }
    div button {
      height: inherit;
      width: 182px;
    }
  }
}






.user_posts {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  padding: 16px 0 33px;

  &_bunch{
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    flex-wrap: wrap;

    max-height: 300px;
    overflow-y: scroll;
    margin: 15px 0;
    padding-right: 10px;
    
    &_item {
      margin-top: 20px;
      div {
        @extend %flex_center;
        padding: 0 0 7px;
      }

      width: 49%;
      border: solid 1px #000;
      padding: 20px;

      text-align: left;
      h4 {
        width: 365px;
        font-size: 17px;
        line-height: 23px;
      }
      p {
        font-size: 12px;
        font-weight: 400;
        line-height: 17px;
        color: rgba(0, 0, 0, 0.41);
      }

      button.main_black_btn {
        position: relative;
        left: 68%;
        margin-top: 10px;
      }
    }
  }
}





.user_publications {
  margin-bottom: 20px;
  &_bunch {
    @extend %flex_center;
    &_item {
      height: 154px;
      width: 154px;
      // background-color: blue;
    }
  }
}





// COMMENTS ==========================================================

.comments {

  &_post {
    padding: 20px;
    font-weight: 400;
    // line-height: 17px;
    font-size: 20px;

    div {
      @extend %flex_center;
      margin-bottom: 10px;
    }
    



  }


  &_bunch {

    &_item {
      margin: 10px 0;
      padding: 10px;
      background-color:#ebe7ff;
      border-radius: 4px;
  
      font-weight: 400;
      line-height: 17px;
  
      div {
        @extend %flex_center;
      }
    }
  }
}









// FOOTER ==================================================
footer {
  background-color: #333;
  color: white;
  height: 50px;
  .footer {
    height: 100%;
    font-size: 12px;
    @extend %flex_center;
    a { color: white }
  }
}

// PRELOADER ========================================================
@keyframes lds-ring {
  0% {transform: rotate(0deg)}
  100% {transform: rotate(360deg)}
}